import React from 'react';
import ReactDOMServer from 'react-dom/server';
import { prettyPrint } from 'html';

class Showcase extends React.Component {

    render() {

        let component = this.props.children;
        let markup = ReactDOMServer.renderToStaticMarkup( component );
        let html = this.props.html || prettyPrint( markup, { unformatted: [], max_char: 200 } );

        return  <div className="__showcase">
                    <div className="__showcase__frame">
                        <div className="__showcase__glass">
                            { component }
                        </div>
                        <div className="__showcase__labels">
                            <span className="__showcase__label __showcase__label--active">HTML</span>
                            <span className="__showcase__label">React</span>
                        </div>
                    </div>
                    <div className="__showcase__description">
                        <pre className="rocket__pre">
                            <code className="language-html">
                                { html }
                            </code>
                        </pre>
                    </div>
                </div>
    }
}

Showcase.defaultProps = {

    component: null,
    html: ''
};

export {

    Showcase
}