import React, { Fragment } from 'react';

const ProgressBar = () => (

    <div className="rocket__progressbar"
         role="progressbar"
         aria-valuenow="20"
         aria-valuemin="0"
         aria-valuemax="100"
         aria-valuetext="Processing 1 of 5 documents"
          style={ { width: '200px' } }
    >
        <div className="rocket__progressbar__label">Processing 1 of 5 documents</div>
        <div className="rocket__progressbar__progress" style={ { width: '20%' } }></div>
    </div>

)

export { ProgressBar }