import React, { Fragment } from 'react';

const Pagination = () => (

    <nav className="rocket__pagination" aria-label="Pagination">
        <button className="rocket__pagination__prev" aria-label="Previous page"></button>
        <button className="rocket__pagination__page" aria-label="Page 1">1</button>
        <button className="rocket__pagination__page rocket__pagination__page--active" aria-current="page">2</button>
        <button className="rocket__pagination__page" aria-label="Page 3">3</button>
        <button className="rocket__pagination__page" aria-label="Page 4">4</button>
        <button className="rocket__pagination__next" aria-label="Next page"></button>
    </nav>
)

export { Pagination }