import React from 'react';

const Autocomplete = () => 

    <div className="rocket__autocomplete">
        <label className="rocket__autocomplete__label"
               id="autocomplete-1-label"
               htmlFor="autocomplete-1-input"
        >
            Search for wines
        </label>
        <div className="rocket__autocomplete__main">
            <div className="rocket__autocomplete__search"
                 role="combobox"
                 aria-expanded="false"
                 aria-owns="autocomplete-1-listbox"
                 aria-haspopup="listbox"
                 id="autocomplete-1"
            >
                <input type="text" 
                       className="rocket__autocomplete__input"
                       aria-autocomplete="list"
                       aria-controls="autocomplete-1-listbox"
                       id="autocomplete-1-input"
                       defaultValue="shir"
                />
                <button aria-label="Close" className="rocket__autocomplete__close"></button>
            </div>
            <div aria-labelledby="autocomplete-1-label"
                 className="rocket__autocomplete__list"
                 role="listbox"
                 id="autocomplete-1-listbox"
            >
                <div role="option" className="rocket__autocomplete__item" aria-selected="false">Parsons Paddock <span className="rocket__autocomplete__match">Shir</span>az 750mL</div>
                <div role="option" className="rocket__autocomplete__item rocket__autocomplete__item--focus" aria-selected="true">Birds Eye View <span className="rocket__autocomplete__match">Shir</span>az 750ml</div>
                <div role="option" className="rocket__autocomplete__item" aria-selected="false">Red Wine <span className="rocket__autocomplete__match">Shir</span>az</div>
                <div role="option" className="rocket__autocomplete__item" aria-selected="false">Red Wine Cabernet <span className="rocket__autocomplete__match">Shir</span>az</div>
            </div>
        </div>
    </div>

export { Autocomplete }