import React, { Fragment } from 'react';

const Dropdown = () => (

    <div className="rocket__dropdown">
        <div className="rocket__dropdown__label" id="dropdown-label-1">Choose a brand</div>
        <div className="rocket__dropdown__main">
            <button className="rocket__dropdown__button" 
                    id="dropdown-button-1" 
                    aria-haspopup="listbox" 
                    aria-labelledby="dropdown-label-1 dropdown-button-1" 
                    aria-expanded="true"
            >
                Brand name
            </button>
            <div className="rocket__listbox" 
                 id="dropdown-list-1" 
                 role="listbox"
                 tabIndex="-1" 
                 aria-labelledby="dropdown-label-1"
            >
                <div className="rocket__listbox__option" id="dropdown-item-1" role="option">Coles</div>
                <div className="rocket__listbox__option rocket__listbox__option--selected" 
                     id="dropdown-item-2"
                     role="option"
                     aria-selected="true"
                >
                    flybuys
                </div>
                <div className="rocket__listbox__option rocket__listbox__option--focused" 
                     id="dropdown-item-3" 
                     role="option"
                >
                    Liquorland
                </div>
                <div className="rocket__listbox__option" id="dropdown-item-4" role="option">First Choice</div>
            </div>
        </div>
    </div>
)

export { Dropdown }