import React, { Fragment } from 'react';

const Table = () => (

    <table className="rocket__table">
        <caption className="rocket__caption">Table caption</caption>
        <thead>
            <tr>
                <th className="rocket__th">Column 1</th>
                <th className="rocket__th">Column 2</th>
                <th className="rocket__th">Column 3</th>
            </tr>
        </thead>
        <tbody>
            <tr>
                <td className="rocket__td">Column 1</td>
                <td className="rocket__td">Column 2</td>
                <td className="rocket__td">Column 3</td>
            </tr>
            <tr>
                <td className="rocket__td">Column 1</td>
                <td className="rocket__td">Column 2</td>
                <td className="rocket__td">Column 3</td>
            </tr>
            <tr>
                <td className="rocket__td">Column 1</td>
                <td className="rocket__td">Column 2</td>
                <td className="rocket__td">Column 3</td>
            </tr>
       </tbody>
    </table>

)

export { Table }